import React from 'react'
import Slider from "react-slick";

import styles from './Carousel.module.scss'
import '../../assets/styles/App.scss'

import image1 from '../../assets/images/carousel2/1.png'
import image2 from '../../assets/images/carousel2/2.png'
import image3 from '../../assets/images/carousel2/3.png'

function Carousel() {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3
      };

    return (
        <div className={styles.carousel}>
            <div className="container">
            <Slider {...settings}>
          <div className={styles.carouselItem}>
            <img src={image1} alt=""/>
          </div>
          <div className={styles.carouselItem}>
            <img src={image2} alt=""/>
          </div>
          <div className={styles.carouselItem}>
            <img src={image3} alt=""/>
          </div>
          <div className={styles.carouselItem}>
            <img src={image1} alt=""/>
          </div>
          
        </Slider>    
            </div>      
        </div>
    )
}

export default Carousel
