import React from 'react'
import Slider from "react-slick";

import './Chosen.scss'

import data from '../../components/ArticleDataBase/ArticleData'
import ArticleCard from '../../components/ArticleCard'

function Sale({ chosenItem }) {

    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
          <div
            className={className}
            style={{ ...style, display: "block", right: '-50px', background: "#6a9ce6", color: 'blue', padding: '5px', boxShadow: '1px 1px 2px 2px rgba(0, 0, 0, .1)', borderRadius: '50%' }}
            onClick={onClick}
          />
        );
      }
      
      function SamplePrevArrow(props) {
        const { className, style, onClick } = props;
        return (
          <div
            className={className}
            style={{ ...style, display: "block", left: '-50px', background: "#6a9ce6", color: 'blue', boxShadow: '1px 1px 2px 2px rgba(0, 0, 0, .1)', padding: '5px', borderRadius: '50%'}}
            onClick={onClick}
          />
        );
      }

      const settings = {
        className: "center",
        centerPadding: "60px",
        dots: false,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        speed: 1000,
        autoplaySpeed: 4000,
        cssEase: "linear",
        pauseOnHover: true,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        afterChange: function(index) {
            console.log(
              `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
            );
          }
      };


    let arrData;

    if(chosenItem === 'add')
        arrData = data.slice(24, 32);
    else if(chosenItem === 'adding')
        arrData = data.slice(8, 22);
    
    return (
        <div className="chosen sale">
            <div className="container">

                <Slider {...settings}>

                    {
                        arrData.map((item, index) => (
                            <ArticleCard 
                                cardImage={item.src}
                                cardTitle={item.title}
                                cardTime={item.time}
                                cardPrice={item.price}
                                id={item.id}
                                key={index}
                            />
                        ))
                    }

                </Slider>   
            </div>
        </div>
    )
}

export default Sale
