import React from 'react'

import styles from './FooterTitle.module.scss'

function FooterTitle({ categoryTitle, titleIcon }) {
    return (
        <div className={styles.footerTitle}>
            <img src={titleIcon} className={styles.titleIcon} alt=""/>
            <p className={styles.title}>{ categoryTitle }</p>
            
        </div>
    )
}

export default FooterTitle
