import React from 'react'

import './FilterItems.scss'
import '../../assets/styles/App.scss'

import data from '../../components/ArticleDataBase/ArticleData'

import ArticleCard from '../ArticleCard'
import ShowMore from '../ShowMore/ShowMore'

function FilterItems() {

    let arr = data.filter(item => item.isTop == true)
    let allData = data.filter(item => item.isTop != true)
    let arrfilter = [...arr, ...allData]

    return (
        <div className="container filterWrapper">
            {
                arrfilter.map((item, index) => (
                    <ArticleCard 
                        cardImage={item.src}
                        cardTitle={item.title}
                        cardTime={item.time}
                        cardPrice={item.price}
                        id={item.id}
                        key={index}
                    />
                ))
            }
            <ShowMore />
        </div>
    )
}

export default FilterItems
