import React from 'react'
import { Link } from 'react-router-dom'

import styles from './Footer.module.scss'
import '../../assets/styles/App.scss'

import FooterTitle from '../../components/FooterTitle'

import appleLogo from '../../assets/images/appStore.svg'
import playStoreLogo from '../../assets/images/android.svg'
import logoFooter from '../../assets/images/darkLogo.svg'
import rightDirRed from '../../assets/images/icons/rightDirRed.svg'
import rightDirYellow from '../../assets/images/icons/rightDirYellow.svg'
import rightDirBlue from '../../assets/images/icons/rightDirBlue.svg'
import rightDirWhite from '../../assets/images/icons/rightDirWhite.svg'

function Footer() {
    return (
        <div className={styles.footer}>
            <div className={styles.footerTop}>
                <div className="container">
                    <div className={styles.footerTopInner}>
                        <div className={styles.topLeft}>
                            <Link to="/">
                                <img src={logoFooter} className={styles.logoFooter} alt=""/>
                            </Link>
                            <h4 className={styles.logoText}>Продай, найди, купи все что ты пожелаешь </h4>
                        </div>
                        <p className={styles.topMiddle}>Веб сайт бесплатных обьявлений | на базе имеется 234 944 999 шт обяления</p>
                        <div className={styles.topRight}>
                            <Link to="https://www.appstore.com" className={styles.apps}  target="_blank">
                                <img src={appleLogo} alt=""/>
                            </Link>
                            <Link to="https://www.playstore.com" className={styles.apps} target="_blank">
                                <img src={playStoreLogo} alt=""/>
                            </Link>
                            
                        </div>
                    </div>
                </div>
            </div>

            <div className={styles.footerMiddle}>
                <div className="container">
                    <div className={styles.middleInner}>
                        <div className={styles.footerCol}>

                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirRed} categoryTitle="Автомобили" />}
                                <Link to="/" className={styles.footerLink}>Легковые авто</Link>
                                <Link to="/" className={styles.footerLink}>Мото</Link>
                                <Link to="/" className={styles.footerLink}>Автобусы</Link>
                                <Link to="/" className={styles.footerLink}>Грузовые авто</Link>
                                <Link to="/" className={styles.footerLink}>Сельхозтехника</Link>
                                <Link to="/" className={styles.footerLink}>Запчасти и Аксессуары</Link>
                                <Link to="/" className={styles.footerLink}>Прицепы и Спецтехника</Link>
                                <Link to="/" className={styles.footerLink}>Водный и Другой транспортa</Link>
                            </div>

                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirYellow} categoryTitle="Дом и Сад" />}
                                <Link to="/" className={styles.footerLink}>Товары для интерьера</Link>
                                <Link to="/" className={styles.footerLink}>Товары для сада</Link>
                                <Link to="/" className={styles.footerLink}>Растения</Link>
                                <Link to="/" className={styles.footerLink}>Продукты питания</Link>
                                <Link to="/" className={styles.footerLink}>Посуда и Кухонная утварь</Link>
                                <Link to="/" className={styles.footerLink}>Хоз.инвентарь и Бытовая химия</Link>
                                <Link to="/" className={styles.footerLink}>Канцтовары и Расходные</Link>
                                <Link to="/" className={styles.footerLink}>Прочие товары для дома</Link>
                            </div>

                        </div>
                        <div className={styles.footerCol}>
                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirYellow} categoryTitle="Недвижимость" />} 
                                <Link to="/" className={styles.footerLink}>Аренда квартир</Link>
                                <Link to="/" className={styles.footerLink}>Аренда домов</Link>
                                <Link to="/" className={styles.footerLink}>Аренда гаражей и Стоянок</Link>
                                <Link to="/" className={styles.footerLink}>Продажа квартир</Link>
                                <Link to="/" className={styles.footerLink}Продажа домов></Link>
                                <Link to="/" className={styles.footerLink}>Продажа земли</Link>
                                <Link to="/" className={styles.footerLink}>Продажа гаражей/Стоянок</Link>
                                <Link to="/" className={styles.footerLink}>Аренда помещений</Link>
                                <Link to="/" className={styles.footerLink}>Продажа помещений</Link>
                                <Link to="/" className={styles.footerLink}>Прочая недвижимость</Link>
                                <Link to="/" className={styles.footerLink}>Обмен недвижимости</Link>
                                <Link to="/" className={styles.footerLink}>Коммерческая недвижимость</Link>
                            </div>

                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirBlue} categoryTitle="Товары стройки и ремонта" />} 
                                <Link to="/" className={styles.footerLink}>Двери и окна</Link>
                                <Link to="/" className={styles.footerLink}>Сантехника</Link>
                                <Link to="/" className={styles.footerLink}>Стройматериалы</Link>
                                <Link to="/" className={styles.footerLink}>Инструменты</Link>
                            </div>
                        </div>
                        <div className={styles.footerCol}>
                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirRed} categoryTitle="Электроника" />} 
                                <Link to="/" className={styles.footerLink}>Телефоны</Link>
                                <Link to="/" className={styles.footerLink}>Компьютеры</Link>
                                <Link to="/" className={styles.footerLink}>Ноутбуки, планшеты</Link>
                                <Link to="/" className={styles.footerLink}>Оргтехника и расходники</Link>
                                <Link to="/" className={styles.footerLink}>ТВ,Фото,Видео и Аудио</Link>
                                <Link to="/" className={styles.footerLink}>Игры и Приставки</Link>
                                <Link to="/" className={styles.footerLink}>Техника для дома и Для себя</Link>
                                <Link to="/" className={styles.footerLink}>Климатическое оборудование</Link>
                                <Link to="/" className={styles.footerLink}>Аксессуары и Комплектующие</Link>
                                <Link to="/" className={styles.footerLink}>Техника для наблюдения</Link>
                                <Link to="/" className={styles.footerLink}>Прочая электроника</Link>
                            </div>
                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirWhite} categoryTitle="Хобби, Отдых и Спорт" />} 
                                <Link to="/" className={styles.footerLink}>Антиквариат и Коллекции</Link>
                                <Link to="/" className={styles.footerLink}>Музыкальные инструменты</Link>
                                <Link to="/" className={styles.footerLink}>Велосипеды</Link>
                                <Link to="/" className={styles.footerLink}>Товары для спорта и отдыха</Link>
                                <Link to="/" className={styles.footerLink}>Книги/Журналы</Link>
                                <Link to="/" className={styles.footerLink}>CD/DVD/Пластинки/Кассеты</Link>
                                <Link to="/" className={styles.footerLink}>Билеты и путешествия</Link>
                                <Link to="/" className={styles.footerLink}>Охота и рыбалка</Link>
                            </div>
                        </div>
                        <div className={styles.footerCol}>
                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirYellow} categoryTitle="Личные вещи" />} 
                                <Link to="/" className={styles.footerLink}>Одежда, обувь, аксессуары</Link>
                                <Link to="/" className={styles.footerLink}>Детская одежда и обувь</Link>
                                <Link to="/" className={styles.footerLink}Детские товары и Игрушки></Link>
                                <Link to="/" className={styles.footerLink}>Школьникам</Link>
                                <Link to="/" className={styles.footerLink}>Часы и украшения</Link>
                                <Link to="/" className={styles.footerLink}>Товары для красоты и здоровья</Link>
                                <Link to="/" className={styles.footerLink}>Разные вещи</Link>
                            </div>
                            <div className={styles.linkGroup}>
                                {<FooterTitle titleIcon={rightDirBlue} categoryTitle="Бизнес и услуги" />} 
                                <Link to="/" className={styles.footerLink}>Сырьё и материалы</Link>
                                <Link to="/" className={styles.footerLink}>Оборудование и аренда</Link>
                                <Link to="/" className={styles.footerLink}>Производство и обработка</Link>
                                <Link to="/" className={styles.footerLink}>Продажа бизнеса и партнерство</Link>
                                <Link to="/" className={styles.footerLink}>Финансовые услуги/Бухгалтерия</Link>
                                <Link to="/" className={styles.footerLink}>Услуги для красоты и здоровья</Link>
                                <Link to="/" className={styles.footerLink}>Услуги курьера и доставки</Link>
                                <Link to="/" className={styles.footerLink}>Реклама, Полиграфия и Маркетинг</Link>
                                <Link to="/" className={styles.footerLink}>Няни, Сиделки и Домработницы</Link>
                                <Link to="/" className={styles.footerLink}>Обучение, курсы и репетиторы</Link>
                                <Link to="/" className={styles.footerLink}>Фото, видео, искусство и дизайн</Link>
                                <Link to="/" className={styles.footerLink}>Перевод и Набор текста</Link>
                                <Link to="/" className={styles.footerLink}>Юридические услуги</Link>
                                <Link to="/" className={styles.footerLink}>Прокат товаров</Link>
                                <Link to="/" className={styles.footerLink}>Туризм и спорт</Link>
                                <Link to="/" className={styles.footerLink}>Праздники и мероприятия</Link>
                            </div>
                        </div>
                    </div>
                    <Link to="/" className={styles.showMore}><span>Покозать еще</span></Link>
                </div>
            </div>
            <div className={styles.footerBottom}>
                <div className="container">
                    <div className={styles.bottomInner}>
                        <Link to="/" className={styles.bottomLink, styles.bottomLinkActive}>Подать объявление</Link>
                        <Link to="/" className={styles.bottomLink}>Объявления</Link>
                        <Link to="/" className={styles.bottomLink}>Магазины</Link>
                        <Link to="/" className={styles.bottomLink}>Помощь</Link>
                        <Link to="/" className={styles.bottomLink}>Безопасность</Link>
                        <Link to="/" className={styles.bottomLink}>Реклама на сайте</Link>
                        <Link to="/" className={styles.bottomLink}>О компании</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Footer
