import React from 'react'
import { Link } from 'react-router-dom'

import './ArticleCard.scss'


function ArticleCard({ cardImage, cardTitle, id, cardTime, cardPrice }) {

    return (
        <Link to={`/singleItem/${id}`} className={`articleCard`}>
            <img src={cardImage} className="cardImage" alt=""/>
            <div className="infoWrapper">
                <h4 className="cardTitle">{cardTitle}</h4>
                <p className="cardTime">{cardTime}</p>
            </div>
            <h3 className="cardPrice">{cardPrice}</h3>
        </Link>
    )
}

export default ArticleCard
