import React from 'react'
import { Link } from 'react-router-dom'

import styles from './FooterTop.module.scss'

import logoFooter from '../../assets/images/darkLogo.svg'
import appleLogo from '../../assets/images/appStore.svg'
import playStoreLogo from '../../assets/images/android.svg'

function FooterTop() {
    return (
        <div className={styles.footerWrapper} >
            <div className={styles.footerTop}>
                <div className="container">
                    <div className={styles.footerTopInner}>
                        <div className={styles.topLeft}>
                            <Link to="/">
                                <img src={logoFooter} className={styles.logoFooter} alt=""/>
                            </Link>
                            <h4 className={styles.logoText}>Продай, найди, купи все что ты пожелаешь </h4>
                        </div>
                        <p className={styles.topMiddle}>Веб сайт бесплатных обьявлений | на базе имеется 234 944 999 шт обяления</p>
                        <div className={styles.topRight}>
                            <Link to="https://www.appstore.com" className={styles.apps}  target="_blank">
                                <img src={appleLogo} alt=""/>
                            </Link>
                            <Link to="https://www.playstore.com" className={styles.apps} target="_blank">
                                <img src={playStoreLogo} alt=""/>
                            </Link>
                            
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FooterTop
