import React from 'react'

import './Chosen.scss'

import data from '../../components/ArticleDataBase/ArticleData'
import ArticleCard from '../../components/ArticleCard'
import ShowMore from '../../components/ShowMore/ShowMore'

function Chosen({ chosenItem }) {
    let arrData;

    if(chosenItem === 'chosen')
        arrData = data.slice(24, 28);
    else if(chosenItem === 'viewed')
        arrData = data.slice(8, 12);
    else if(chosenItem === 'sale')
        arrData = data.slice(12, 16);
    
    return (
        <div className="chosen">
            <div className="container">
                <div className="chosenInner">
                {
                    arrData.map((item, index) => (
                        <ArticleCard 
                            cardImage={item.src}
                            cardTitle={item.title}
                            cardTime={item.time}
                            cardPrice={item.price}
                            id={item.id}
                            key={index}
                        />
                    ))
                }
                </div>
                <ShowMore />
            </div>
        </div>
    )
}

export default Chosen
