import React from 'react'

import styles from './Pages.module.scss'
import './Main.scss'

import Header from '../containers/Header'
import HeaderNavbar from '../components/HeaderNavbar';
import FilterSettings from '../components/FilterSettings/'
import FilterItems from '../components/FilterItems/FilterItems'
import SearchingCarousel from '../components/SearchingCarousel/SearchingCarousel'
import InterestingCarousel from '../components/InterestingCarousel/InterestingCarousel'

import Footer from '../containers/Footer'

import search from '../assets/images/icons/search.svg'


function TopFilter() {
    return (
        <div className={styles.topFilter}>
            <Header navbar={<HeaderNavbar />} logoText="Продай, найди, купи все что пожелаешь…"/>
            <FilterSettings />
            <FilterItems />

            <div className="container searching">
                <img src={search} alt=""/>
                <p className="searching-title">СЕЙЧАС ИЩУТ</p>
            </div>
            <SearchingCarousel searching={search}/>

            <p className="container interesting">САМОЕ ИНТЕРЕСНОЕ</p>
            <InterestingCarousel searching={search}/>

            {/* <div className="container interesting">
                
            </div> */}

            <Footer />

        </div>
    )
}

export default TopFilter
