import React, { useState } from 'react'

import Header from '../containers/Header';
import HeaderBottom from '../components/HeaderBottom';
import CarouselOption from '../containers/CarouselOption';
import NewProduct from '../containers/NewProduct';
import BestProduct from '../containers/BestProduct';
import Carousel from '../containers/Carousel2/Carousel';
import Chosen from '../containers/ProductType2/Chosen';
import Footer from '../containers/Footer'

import '../assets/styles/App.scss';
import './Main.scss'

function Home() {
//   console.log(data.map(item => item.isTop));

  const [ product, setProduct ] = useState('new')
  const [ chosenItem, setChosenItem ] = useState('chosen')
  
  return (
    <div>
        <Header headerBottom={<HeaderBottom/>} />
        <CarouselOption />
        
        <div className="container">
            <div className="btn-wrapper">
                <button className={`button ${product === 'new' ? 'active' : ''}`} onClick={() => setProduct('new')}>Новые объявления</button>
                <button className={`button ${product === 'best' ? 'active' : ''}`} onClick={() => setProduct('best')}>Лучщие предложение</button>
            </div>
        </div>
        <NewProduct product={product}/>
        <BestProduct product={product}/>

        <Carousel />
       
        <div className="container">
            <div className="btn-wrapper">
                <button className={`button ${chosenItem === 'chosen' ? 'active' : ''}`} onClick={() => setChosenItem('chosen')}>Просмотренные</button>
                <button className={`button ${chosenItem === 'viewed' ? 'active' : ''}`} onClick={() => setChosenItem('viewed')}>Выбор редакции</button>
                <button className={`button ${chosenItem === 'sale' ? 'active' : ''}`} onClick={() => setChosenItem('sale')}>Сниженные цены</button>
            </div>
        </div>
        <Chosen chosenItem={chosenItem}/>
        


        
        {/* <ProductTypeSmall pageRoute11="viewed" pageTitle11="Просмотренные" pageRoute22="chosen" pageTitle22="Выбор редакции" pageRoute33="sale" pageTitle33="Сниженные цены"/> */}
        
        {/* <NewProduct></NewProduct> */}
                 
        <Footer />
    </div>
  )
}

export default Home
