import React from 'react'
import { Link } from 'react-router-dom'

import styles from './HeaderNavbar.module.scss'

function HeaderNavbar() {
    return (
        <div className={styles.navbar}>
            <Link to="/singleItem/14" className={styles.navLink}>Объявления</Link>
            <Link to="/singleItem/7" className={styles.navLink}>Магазины</Link>
            <Link to="/singleItem/28" className={styles.navLink}>Для бизнеса</Link>
            <Link to="/singleItem/15" className={styles.navLink}>Помощь</Link>
        </div>
    )
}

export default HeaderNavbar
