import React from 'react'
import { span } from 'react-router-dom'

import './Table.scss'

function Table() {
    return (
        <div className="table">
            <div className="table-row">
                <div className="table-col">
                    <p className="col-title">Количество комнат:</p>
                    <span className="col-data">6</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Общая площадь:</p>
                    <span className="col-data">40 м2</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Этаж:</p>
                    <span className="col-data">32</span>
                </div>
            </div>

            <div className="table-row">
                <div className="table-col">
                    <p className="col-title">Этажность дома:</p>
                    <span className="col-data">6</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Состояние квартиры:</p>
                    <span className="col-data">Первая сдача </span>
                </div>
                <div className="table-col">
                    <p className="col-title">Этаж:</p>
                    <span className="col-data">32</span>
                </div>
            </div>

            <div className="table-row">
                <div className="table-col">
                    <p className="col-title">Планировка</p>
                    <span className="col-link">Раздельная</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Год постройки/сдачи</p>
                    <span className="col-link">Сдача в 2017</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Ремонт</p>
                    <span className="col-data">Авторский проект</span>
                </div>
            </div>

            <div className="table-row">
                <div className="table-col">
                    <p className="col-title">Санузел</p>
                    <span className="col-link">2 санузла и более</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Меблирована</p>
                    <span className="col-link">Да</span>
                </div>
                <div className="table-col">
                    <p className="col-title">Высота потолков</p>
                    <span className="col-data">32</span>
                </div>
            </div>
            <div className="table-row">
                <div className="table-col-bottom">
                    <p className="col-title">Рядом есть</p>
                    <span className="col-link">Больница, поликлиника, Детская, площадка, Детский сад, Остановки, Парк, зелёная зона, Развлекательные заведения, Рестораны, кафе	</span>
                </div>
            </div>
        </div>
    )
}

export default Table
