import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import Slider from "react-slick";

import styles from './CarouselOption.module.scss'
import './main.scss'

import slide from '../../assets/images/carousel-icons/slide.svg'
import slide1 from '../../assets/images/carousel-icons/slide1.svg'
import slide2 from '../../assets/images/carousel-icons/slide2.svg'
import slide3 from '../../assets/images/carousel-icons/slide3.svg'
import slide4 from '../../assets/images/carousel-icons/slide4.svg'
import slide5 from '../../assets/images/carousel-icons/slide5.svg'
import slide6 from '../../assets/images/carousel-icons/slide6.svg'
import slide7 from '../../assets/images/carousel-icons/slide7.svg'
import slide8 from '../../assets/images/carousel-icons/slide8.svg'


function CarouselOption() {

    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
          <div
            className={className}
            style={{ ...style, display: "block", right: '-50px', background: "#6a9ce6", color: 'blue', padding: '5px', boxShadow: '1px 1px 2px 2px rgba(0, 0, 0, .1)', borderRadius: '50%' }}
            onClick={onClick}
          />
        );
      }
      
      function SamplePrevArrow(props) {
        const { className, style, onClick } = props;
        return (
          <div
            className={className}
            style={{ ...style, display: "block", left: '-50px', background: "#6a9ce6", color: 'blue', boxShadow: '1px 1px 2px 2px rgba(0, 0, 0, .1)', padding: '5px', borderRadius: '50%'}}
            onClick={onClick}
          />
        );
      }

    const settings = {
        className: "center",
        centerPadding: "60px",
        dots: false,
        infinite: true,
        slidesToShow: 9,
        slidesToScroll: 1,
        autoplay: true,
        speed: 1000,
        autoplaySpeed: 4000,
        cssEase: "linear",
        pauseOnHover: true,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        afterChange: function(index) {
            console.log(
              `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
            );
          }
      };

      const [modal, setModal] = useState(false)
      console.log(modal);

    return (
        <div className="container">
            <div className={styles.carouselOption}>
        
                <Slider {...settings}>

                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide1} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide2} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide3} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide4} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide5} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide6} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide7} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide8} alt=""/>
                    </Link>
                    <Link to="/" className={styles.slideImage}>
                        <img onClick={()=> setModal(!modal)} src={slide4} alt=""/>
                    </Link>
                    
                </Slider>
            </div>
                <div className={`modal ${modal ? 'active' : ''}`}>
                    <div className="modal-col">
                        <Link to="/topfilter" className="modal-link">Телефоны</Link>
                        <Link to="/topfilter" className="modal-link">Компьютеры</Link>
                        <Link to="/topfilter" className="modal-link">Фото / видео</Link>
                        <Link to="/topfilter" className="modal-link">Тв / видеотехника</Link>
                        <Link to="/topfilter" className="modal-link">Аудиотехника</Link>
                    </div>
                    <div className="modal-col">
                        <Link to="/topfilter" className="modal-link">Игры и игровые приставки</Link>
                        <Link to="/topfilter" className="modal-link">Техника для дома</Link>
                        <Link to="/topfilter" className="modal-link">Техника для кухни</Link>
                        <Link to="/topfilter" className="modal-link">Климатическое оборудование</Link>
                        <Link to="/topfilter" className="modal-link">Индивидуальный уход</Link>
                    </div>
                    <div className="modal-col">
                        <Link to="/topfilter" className="modal-link">Аксессуары и комплектующие</Link>
                        <Link to="/topfilter" className="modal-link">Прочая электроника</Link>
                        <Link to="/topfilter" className="modal-link">Телефоны</Link>
                        <Link to="/topfilter" className="modal-link">Компьютеры</Link>
                        <Link to="/topfilter" className="modal-link">Фото / видео</Link>
                    </div>
                    <div className="modal-col">
                        <Link to="/topfilter" className="modal-link">Телефоны</Link>
                        <Link to="/topfilter" className="modal-link">Компьютеры</Link>
                        <Link to="/topfilter" className="modal-link">Фото / видео</Link>
                        <Link to="/topfilter" className="modal-link">Тв / видеотехника</Link>
                        <Link to="/topfilter" className="modal-link">Аудиотехника</Link>
                    </div>
                </div>
        </div>
    )
}

export default CarouselOption
