import React, { useState } from 'react'
import { Link } from 'react-router-dom'

import styles from './Pages.module.scss'
import './Main.scss'

import data from '../components/ArticleDataBase/ArticleData';

import Header from '../containers/Header';
import HeaderBottom from '../components/HeaderBottom/HeaderBottom';
import HeaderNavbar from '../components/HeaderNavbar/HeaderNavbar';
import SingleItemRight from '../containers/SingleItemRight/SingleItemRight';
import SingleItemLeft from '../containers/SingleItemLeft/SingleItemLeft';
import Sale from '../containers/ProductType2/Sale'
import Footer from '../containers/Footer'

function SingleItem() {

    let arr = window.location.pathname.split('/');
    let index = arr[arr.length - 1] - 1;

    const [ product, setProduct] = useState('add')

    return (
        <div className={styles.singleItem}>
            <Header logoText="Продай, найди, купи все что пожелаешь…" navbar={<HeaderNavbar/>} headerBottom={<HeaderBottom />} />

            <div className="container">
                <Link to="/" className={styles.routePath}>Объявления   /   Ташкент Недвижимость   /   Ташкент Квартиры   /   Ташкент Продажа   /   Новостройки Ташкент</Link>
                <div className={styles.articleTitle}>{data[index].title}</div>

                <div className={styles.middleSection}>
                    <SingleItemLeft currentData={data[index]} />
                    <SingleItemRight currentData={data[index]} />
                </div>

                <div className="btn-wrapper">
                    <button className={`button ${product === 'add' ? 'active' : ''}`} onClick={() => setProduct('add')}>Похожие объявления</button>
                    <button className={`button ${product === 'adding' ? 'active' : ''}`} onClick={() => setProduct('adding')}>Обьявление автора</button>
                </div>
            </div>

            <Sale chosenItem={product}/>
            <Footer />

            
        </div>
    )
}

export default SingleItem
