import React from 'react'

import '../Main.scss'
// import styles from '../Pages.module.scss'


import FooterTop from '../../components/FooterTop/FooterTop'
import Header from '../../containers/Header'
import HeaderBottom from '../../components/HeaderBottom'
import HeaderNavbar from '../../components/HeaderNavbar'
import NameForm from '../../components/Forms/LoginName'

import LoginInfo from '../../assets/images/registerInfo.png'


function Login() {
    return (
        <div className="login">
            <Header headerBottom={<HeaderBottom/>} navbar={<HeaderNavbar/>} logoText="Продай, найди, купи все что пожелаешь…" />
            <div className="container">
                    <div className="login-main">
                        <div className="login-left">
                            <h3 className="form-title">Бесплатная регистрация</h3>
                            <NameForm/>
                            <p className="or">или</p>
                            <p className="form-data">Войдите в систему, если у вас уже есть аккаунт kivi.uz</p>
                        </div>
                        <div className="login-right">
                            <img src={LoginInfo} alt=""/>
                        </div>
                    </div>
            </div>
            <FooterTop />
        </div>
    )
}

export default Login
