import React, { useState } from 'react'
import { Link } from 'react-router-dom'

import styles from './SingleItemRight.module.scss'
import './main.scss'

import Table from '../../components/Table'

function SingleItemRight({ currentData }) {

    let today = new Date();
    let time = today.getHours() + "." + today.getMinutes() + "." + today.getSeconds();
    
    const [showNumber1, setShowNumber1] = useState(true)
    const [showNumber2, setShowNumber2] = useState(true)

    const [toggle, setToggle] = useState(true)
    const [toggle2, setToggle2] = useState(true)

    const showNum1 = () => {
        setShowNumber1(!showNumber1)
        setToggle(!toggle)
    }

    const showNum2 = () => {
        setShowNumber2(!showNumber2)
        setToggle2(!toggle2)
    }

    return (

        <div className={styles.singleItemRight}>
            <div className={styles.top}>
                <p className={styles.topData}>Артикул: 4418970 </p>
                <p className={styles.topData}>обновлено: {time}  добавлено: {time} </p>
            </div>
            <p className={styles.productPrice}>{currentData.price}</p>
            <p className={styles.address}>Ташкент, Ташкентская область, Юнусабадский район </p>

            <div className={styles.numbers}>
                <a href={showNumber1 ? 'javascript:void(0)' : 'tel:998911661424'} className={styles.number}>99891 166 {showNumber1 ? '****' : '1424'}</a>
                <button className={styles.toggleBtn} onClick={showNum1}>{toggle ? 'Покозать польностю' : 'Спрятать'}</button>
            </div>
            <div className={styles.numbers}>
                <a href={showNumber2 ? 'javascript:void(0)' : 'tel:998911661122'} className={styles.number}>99891 166 {showNumber2 ? '****' : '1122'}</a>
                <button className={styles.toggleBtn} onClick={showNum2}>{toggle2 ? 'Покозать польностю' : 'Спрятать'}</button>
            </div>
            <p className={styles.author}>Автор обьявлении: <span>Зухриддин Темиров</span></p>
            <div className={styles.btnWrapper}>
                <Link className={styles.btnItem}>Написать автору</Link>
                <Link className={styles.btnItem}>Предложить свою цену</Link>
            </div>
            {<Table />}
            <p className={styles.rightText}>Срочно продаётся 3х ком на 1 этоже 7 этажном кирпичном доме. Новостройка Голден Хаус жылой комплекс Етти Чинор Очень качественный и дорогой ремонт. Комнаты полнастю разделные болшые. 2 сан узла. Для лишних вещей есть кладовка.Ремонт шыкарный обсалютно новый. </p>

            <p className={styles.rightText}>Ремонт делолся из очень дорогова материяла. Дорогая класическая кухонная мебель. Джаккузи,Тёплые полы в каждой комнате. Дорогие люстры вовсех комнатах решодки на окнах. Теретория охранается. Всё предусмотренно для детей деские площядки качели итд. Торг наместе. Торопитесь продаю нужны денги. Первый золотой этаж и паследний в этом коплексе. Торопитесь.</p>

            <div className={styles.textInfo}>
                <p className={styles.infoLeft}>Просмотры:10958</p>
                <p className={styles.infoRight}>Пожаловатся</p>
            </div>

        </div>
    )
}

export default SingleItemRight
