import React from 'react'
import Slider from "react-slick";

import './InterestingCarousel.scss'
// import '../../assets/styles/App.scss'

import data from '../ArticleDataBase/ArticleData'

import ArticleCard from '../ArticleCard'


function InterestingCarousel({ searching }) {

    let arrData = data.slice(16, 32)

    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
          <div
            className={className}
            style={{ ...style, display: "block", right: '-50px', background: "#6a9ce6", color: 'blue', padding: '5px', boxShadow: '1px 1px 2px 2px rgba(0, 0, 0, .1)', borderRadius: '50%' }}
            onClick={onClick}
          />
        );
      }

      const settings = {
        className: "center",
        centerPadding: "60px",
        dots: false,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        autoplay: true,
        speed: 1000,
        autoplaySpeed: 4000,
        cssEase: "linear",
        pauseOnHover: true,
        nextArrow: <SampleNextArrow />,
        afterChange: function(index) {
            console.log(
              `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
            );
          }
      };


    return (
        <div className="container interesting-area">

            <Slider {...settings}>
                    {  
                        arrData.map((item, index) => (
                            <ArticleCard 
                                cardImage={item.src}
                                cardTitle={item.title}
                                cardTime={item.time}
                                cardPrice={item.price}
                                id={item.id}
                                key={index}
                            />
                        ))
                    }
            </Slider>    
        </div>
    )
}

export default InterestingCarousel
