import React, { useState } from "react";

import "./FilterSettings.scss";
import "../../assets/styles/App.scss";

import SearchIcon from "../../assets/images/icons/search.svg";
import pin from "../../assets/images/icons/location.svg";
import close from "../../assets/images/icons/close.svg";
import more from "../../assets/images/icons/more.svg";

function FilterSettings() {

    

  return (
    <div className="filter">
      <div className="container">
        <div className="search-wrapper">
          <img src={SearchIcon} alt="" />
          <input
            type="search"
            placeholder="Введите название обявления или  выберите из списка"
          />
        </div>

        <div className="categories">
          <div className="categories-wrapper blue">
            <p>Интерсное</p>
          </div>
          <div className="categories-wrapper cloud">
            <p>Топ</p>
          </div>
          <div className="categories-wrapper green">
            <p>Отличные предложение</p>
          </div>
          <div className="categories-wrapper red">
            <p>Супер цена</p>
          </div>
          <div className="categories-wrapper city">
            <p className="categories-text">г.Ташкент</p>
            <img src={pin} alt="" />
          </div>
          <div className="categories-wrapper city">
            <p className="categories-text">г.Термез</p>
            <img src={pin} alt="" />
          </div>
          <div className="categories-wrapper city">
            <p className="categories-text">г.Самарканд</p>
            <img src={pin} alt="" />
          </div>
        </div>

        <div className="devices-wrapper">
          <p className="devices-title">Вы сейчас в рубрике</p>
          <div className="devices-options-wrapper">
            <div className="options-left">
              <div className="option">
                <p className="option-text">Электроника</p>
                <button>
                  <img src={close} alt="" />
                </button>
              </div>

              <div className="option">
                <p className="option-text">Мониторы</p>
                <button>
                  <img src={close} alt="" />
                </button>
              </div>
            </div>
            <img src={more} alt="" />
          </div>
        </div>

        
        <div className="selections-wrapper">
          <div className="selections-left">
            <select name="" id="">
              <option value="a">Состояние</option>
              <option value="a">Состояние</option>
              <option value="a">Состояние</option>
            </select>

            <select name="" id="">
              <option value="a">Тип объявщика</option>
              <option value="a">Тип объявщика</option>
              <option value="a">Тип объявщика</option>
            </select>

            <select name="" id="">
              <option value="a">Ташкент + Мирза улугбекский район</option>
              <option value="a">Состояние</option>
              <option value="a">Состояние</option>
            </select>
          </div>

          <button>Применить</button>
        </div>
      </div>
    </div>
  );
}

export default FilterSettings;
